.button-deactivated {
  font-size: 1rem;
  /* padding: 5px 10px; */
  color:   #585858;
	cursor: pointer;
	text-align: center;
	margin: 0;
	padding: 0;
}

.button-activated {
  font-size: 1rem;
  /* padding: 5px 10px; */
  font-weight: bold;
  color: #1565c0;
	cursor: pointer;
	text-align: center;
	margin: 0;
	padding: 0;
}

img.resize-like {
  max-width: 30%;
  max-height: 30%;
	cursor: pointer;
	margin: 0;
	padding: 0;
}

img.resize-dislike {
  max-width: 30%;
  max-height: 30%;
  cursor: pointer;
	transform: rotate(180deg);
	margin: 0;
	padding: 0;
}